import React from 'react';

const BlogPost = ({ index, post }) => {

  index++;

  if(index % 3 === 0) {
    return (
      <div className="post">
        <div className="post__description"></div>
        <div className="post__image"></div>
        <div className="post__video"></div>
      </div>
    );
  }
  else if(index % 2 === 0) {
    return (
      <div className="post --is-even">
        <div className="post__description"></div>
        <div className="post__image"></div>
      </div>
    );
  }

  return (
    <div className="post">
      <div className="post__description"></div>
      <div className="post__image"></div>
    </div>
  );
};

export default BlogPost;
